// src/components/Favorites/index.js
import React from 'react';

const Favorites = ({ section }) => {
  return (
    <div>
      {section === 'personal' && <div></div>}
      {section === 'recommend' && <div></div>}
      {!section && <div></div>}
    </div>
  );
};

export default Favorites;
