import React, { useState } from 'react';
import Header from '../../components/Header/Header';
import { Pagination } from 'antd';
import './index.scss';

const PAGE_SIZE = 5;

const Mentioned = () => {
  const [posts, setPosts] = useState([]);
  const [newPost, setNewPost] = useState('');
  const [currentPage, setCurrentPage] = useState(1);

  const handleAddPost = () => {
    if (newPost.trim()) {
      setPosts([...posts, { content: newPost, id: Date.now() }]);
      setNewPost('');
    }
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const startIndex = (currentPage - 1) * PAGE_SIZE;
  const currentPosts = posts.slice(startIndex, startIndex + PAGE_SIZE);

  return (
    <div className="mention-page">
      <Header />
      <div className="main-content">
        <div className="left-section">
          <div className="post-container">
            <div className="post-wrapper">
              <div className="add-post">
                <div className="textarea-container">
                  <textarea
                    value={newPost}
                    onChange={(e) => setNewPost(e.target.value)}
                    placeholder="..."
                  />
                  <button className="post-button" onClick={handleAddPost}></button>
                </div>
              </div>
              <div className="mention-content-wrapper">
                <div className="posts-list">
                  {currentPosts.length > 0 ? (
                    currentPosts.map((post) => (
                      <div key={post.id} className="post-item">
                        <p>{post.content}</p>
                      </div>
                    ))
                  ) : (
                    <p></p>
                  )}
                </div>
                <div className="pagination-container">
                  <Pagination
                    current={currentPage}
                    pageSize={PAGE_SIZE}
                    total={posts.length}
                    onChange={handlePageChange}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="right-section">
          <div className="tags">
            <h3></h3>
            <div className="tag-list">
              <div className="tag-item">1</div>
              <div className="tag-item">2</div>
              <div className="tag-item">3</div>
              <div className="tag-item">4</div>
              <div className="tag-item">5</div>
              <div className="tag-item">6</div>
            </div>
          </div>
          <div className="ranking">
            <h3></h3>
            <ol>
              <li>1</li>
              <li>2</li>
              <li>3</li>
              <li>4</li>
              <li>5</li>
            </ol>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Mentioned;
