import React, { useState } from 'react';
import { Menu } from 'antd';
import { SettingOutlined, StarOutlined, CommentOutlined, ShareAltOutlined } from '@ant-design/icons';
import Settings from '../../components/Settings'; // 
import Favorites from '../../components/Favorites'; // 
import Comments from '../../components/Comments'; // 
import SharedResources from '../../components/SharedResources'; // 
import Header from '../../components/Header/Header'; // 
import './index.scss';

const Profile = () => {
  const [selectedKey, setSelectedKey] = useState('1');

  const renderContent = () => {
    //  selectedKey 
    switch (selectedKey) {
      case '1':
        return <Settings section="personal" />;
      case '2':
        return <Settings section="system" />;
      case '3':
        return <Favorites section="personal" />;
      case '4':
        return <Favorites section="recommend" />;
      case '5':
        return <Comments section="my" />;
      case '6':
        return <Comments section="manage" />;
      case '7':
        return <SharedResources section="resources" />;
      case '8':
        return <SharedResources section="requests" />;
      default:
        return <Settings section="personal" />;
    }
  };

  return (
    <div className="profile-page">
      <Header /> {/*  Profile  Header */}

      <div className="profile-content-wrapper">
        <div className="profile-sider">
          <div className="profile-avatar">
            <img src="/path/to/avatar.jpg" alt="Avatar" />
            <h2></h2>
          </div>
          <Menu 
            mode="inline"
            selectedKeys={[selectedKey]}
            onClick={({ key }) => {
              console.log('Menu item clicked:', key); // 
              setSelectedKey(key);
            }}
          >
            <Menu.SubMenu key="settings" icon={<SettingOutlined />} title="">
              <Menu.Item key="1"></Menu.Item>
              <Menu.Item key="2"></Menu.Item>
            </Menu.SubMenu>
            <Menu.SubMenu key="favorites" icon={<StarOutlined />} title="">
              <Menu.Item key="3"></Menu.Item>
              <Menu.Item key="4"></Menu.Item>
            </Menu.SubMenu>
            <Menu.SubMenu key="comments" icon={<CommentOutlined />} title="">
              <Menu.Item key="5"></Menu.Item>
              <Menu.Item key="6"></Menu.Item>
            </Menu.SubMenu>
            <Menu.SubMenu key="shared" icon={<ShareAltOutlined />} title="">
              <Menu.Item key="7"></Menu.Item>
              <Menu.Item key="8"></Menu.Item>
            </Menu.SubMenu>
          </Menu>
        </div>
        <div className="profile-content">
          {renderContent()}
        </div>
      </div>
    </div>
  );
};

export default Profile;


