// src/components/SharedResources/index.js
import React from 'react';

const SharedResources = ({ section }) => {
  return (
    <div>
      {section === 'resources' && <div></div>}
      {section === 'requests' && <div></div>}
      {!section && <div></div>}
    </div>
  );
};

export default SharedResources;
