import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, Form, Input, Button, Typography, Alert } from 'antd';
import './index.scss';

const { Title } = Typography;

const RegisterPage = () => {
  const [email, setEmail] = useState('');
  const [verificationCode, setVerificationCode] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const navigate = useNavigate();

  // 
  const handleSubmit = async () => {
    if (!email || !verificationCode || !username || !password || !confirmPassword) {
      setError('');
      return;
    }
  
    if (password !== confirmPassword) {
      setError('');
      return;
    }
  
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/auth/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, verificationCode, username, password, confirmPassword }),
      });
  
      if (response.ok) {
        const data = await response.json();
        setSuccess(data.message);
        setTimeout(() => {
          navigate('/login');
        }, 2000);
      } else {
        const errorData = await response.json();
        setError(errorData.message || '');
      }
    } catch (error) {
      setError('');
    }
  };
  

  // 
  const handleGetVerificationCode = async () => {
    if (!email) {
      setError('');
      return;
    }
  
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL}/auth/determine/email?email=${encodeURIComponent(email)}`);
      if (response.ok) {
        alert('');
      } else {
        const errorData = await response.json();
        setError(errorData.message || '');
      }
    } catch (error) {
      setError('');
    }
  };
  

  return (
    <div className="register-container">
      <Card
        title={<Title level={2}></Title>}
        className="register-card"
        bordered={false}
      >
        {error && <Alert message={error} type="error" showIcon />}
        {success && <Alert message={success} type="success" showIcon />}
        <Form
          name="register"
          onFinish={handleSubmit}
          layout="vertical"
        >
          <Form.Item
            name="email"
            label=""
            rules={[{ required: true, message: '!' }]}
            className="form-item"
          >
            <Input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </Form.Item>
          
          <Form.Item
            name="verificationCode"
            label=""
            rules={[{ required: true, message: '!' }]}
            className="form-item"
          >
            <Input
              type="text"
              value={verificationCode}
              onChange={(e) => setVerificationCode(e.target.value)}
            />
          </Form.Item>
          <Form.Item
            name="username"
            label=""
            rules={[{ required: true, message: '!' }]}
            className="form-item"
          >
            <Input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
          </Form.Item>
          <Form.Item
            name="password"
            label=""
            rules={[{ required: true, message: '!' }]}
            className="form-item"
          >
            <Input.Password
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="form-item"
            />
          </Form.Item>
          <Form.Item
            name="confirmPassword"
            label=""
            rules={[{ required: true, message: '!' }]}
            className="form-item"
          >
            <Input.Password
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              className="form-item"
            />
          </Form.Item>
          <Form.Item>
            <Button type="default" onClick={handleGetVerificationCode}>
              
            </Button>
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit" block>
              
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};

export default RegisterPage;


